import React, {FC, useEffect, useState} from "react";
import {
    BusinessIssue,
    DevIssue,
    Epic,
    IssueInfo,
    IssueType,
    TableResult,
    TableResultBoardEntry,
    TableResultBoardEntryDevColumn
} from "./TableResult";
import {ProgressBar} from "react-progressbar-fancy";
import {JiraFullPriority, JiraSearchIssue, JiraSearchResult} from "./JiraTypes";
import {ConfigV2, loadConfig} from "./Config";
import {JiraOrderedPriority, SimpleCallback} from "./CommonTypes";
import './MainTable.css';
import {Tooltip} from "react-tooltip";

export const MainTable: FC<{ baseUrl: string, businessBoardId: string, showConfigCallback: SimpleCallback }> =
    ({baseUrl, businessBoardId, showConfigCallback}) => {

        const [loadProgress, setLoadProgressOrig] = useState<number[]>([0, 0]);
        let loadProgressLocal: number[] = [0, 0];
        const [config, setConfig] = useState(new ConfigV2());
        const [selectedView, setSelectedViewOrig] =
            useState(Number.parseInt(localStorage.getItem('jira.goose.selectedView.' + businessBoardId) ?? "0"));

        function setSelectedView(value: number) {
            localStorage.setItem('jira.goose.selectedView.' + businessBoardId, value.toString());

            setSelectedViewOrig(value);

            loadData().then();
        }

        function setLoadProgress(vals: number   []) {
            loadProgressLocal = vals;
            setLoadProgressOrig(vals);
        }

        function incLoadProgress() {
            loadProgressLocal[0]++;
            setLoadProgressOrig(loadProgressLocal);
        }

        function addProgress(value: number) {
            setLoadProgress([loadProgressLocal[0] + value, loadProgressLocal[1]]);
        }

        const [tableResult, setTableResult] = useState<TableResult>(
            new TableResult(undefined, undefined, undefined, undefined, 0, {}));

        async function loadDevIssues(aConfig: ConfigV2, newTableResult: TableResult): Promise<JiraSearchIssue[]> {

            let issuesToLoad: String[] = newTableResult.business.map(i => i.key);
            newTableResult.epics.forEach(e => e.business.forEach(i => issuesToLoad.push(i.key)));

            try {

                setLoadProgress([issuesToLoad.length, issuesToLoad.length * 3]);

                let url = baseUrl + "/rest/api/latest/search";

                let req = {
                    jql: "",
                    startAt: 0,
                    maxResults: 100,
                    expand: ["changelog"],
                    fields: ["created", "issuelinks", "projectId", "assignee", "status", "priority"]
                };
                if (aConfig.blockField !== undefined && aConfig.blockField !== "") {
                    req.fields.push(aConfig.blockField)

                }

                let fetchParams = [];

                while (issuesToLoad.length > 0) {

                    let issKeys = issuesToLoad.splice(0, 100);

                    req.jql = "issuekey in (" + issKeys.join(",") + ")";

                    const param = {
                        method: 'POST',
                        headers: {
                            'Content-type': 'application/json'
                        },
                        body: JSON.stringify(req),
                    };


                    fetchParams.push(fetch(url,
                        param).then(res => {
                        addProgress(issKeys.length);
                        return res.json();
                    }));
                }
                let data: JiraSearchResult[] = await Promise.all(fetchParams)
                return data.flatMap(arr => arr.issues);
            } catch (e) {
                console.warn(e);
                return [];
            }
        }

        async function loadIssuesInfo(aConfig: ConfigV2, newTableResult: TableResult) {

            let issuesToLoad = newTableResult.findIssuesToLoad();

            setLoadProgress([issuesToLoad.length * 2, issuesToLoad.length * 3]);

            try {

                let url = baseUrl + "/rest/api/latest/search";
                let req = {
                    jql: "",
                    startAt: 0,
                    maxResults: 100,
                    expand: [],
                    fields: ["created", "projectId", "assignee", "status", "priority"]
                };

                if (aConfig.blockField !== undefined && aConfig.blockField !== "") {
                    req.fields.push(aConfig.blockField)
                }

                let fetchParams = [];

                while (issuesToLoad.length > 0) {

                    let issKeys = issuesToLoad.splice(0, 100);

                    req.jql = "issuekey in (" + issKeys.join(",") + ")";

                    const param = {
                        method: 'POST',
                        headers: {
                            'Content-type': 'application/json'
                        },
                        body: JSON.stringify(req),
                    };


                    fetchParams.push(fetch(url,
                        param).then(res => {
                        addProgress(issKeys.length);
                        return res.json();
                    }));
                }

                let data: JiraSearchResult[] = await Promise.all(fetchParams)
                newTableResult.fillIssues(data.flatMap(arr => arr.issues));
            } catch (e) {
                console.warn(e);
            }
        }

        async function loadData() {


            setLoadProgress([0, 18]);

            const prioritiesUrl = baseUrl + "/rest/api/2/priority";
            let prioritiesFetch = fetch(prioritiesUrl).then(res => {
                incLoadProgress();
                return res.ok ? res.json() : undefined;
            });

            let [aConfig, prioritiesData] = await Promise.all([loadConfig(baseUrl, businessBoardId), prioritiesFetch]);

            setLoadProgress([2, 18]);

            const priorities: { [id: string]: JiraOrderedPriority } = {};
            (prioritiesData as JiraFullPriority[]).forEach((priority, index) =>
                priorities[priority.id] = Object.assign(priority, {order: index}));

            setConfig(aConfig);

            let bBoardUrl = baseUrl +
                "/rest/greenhopper/1.0/xboard/work/allData.json?rapidViewId=" + businessBoardId;
            if (aConfig.view[selectedView].bQF.length > 0) {
                bBoardUrl += "&activeQuickFilters=" + aConfig.view[selectedView].bQF.join("&activeQuickFilters=");
            }
            let bBoardDataFetch = fetch(bBoardUrl).then(res => {
                incLoadProgress();
                return res.ok ? res.json() : undefined;
            });

            const bEditModelUrl = baseUrl +
                "/rest/greenhopper/1.0/rapidviewconfig/editmodel.json?rapidViewId=" + businessBoardId;

            let bEditModelFetch = fetch(bEditModelUrl).then(res => {
                incLoadProgress();
                return res.ok ? res.json() : undefined;
            });

            const dEditModelUrl = baseUrl +
                "/rest/greenhopper/1.0/rapidviewconfig/editmodel.json?rapidViewId=" + aConfig.devBoardId;

            let dEditModelFetch = fetch(dEditModelUrl).then(res => {
                incLoadProgress();
                return res.ok ? res.json() : undefined;
            });

            let dBoardDataFetch = undefined;

            if (aConfig.view[selectedView].showDev) {
                let dBoardUrl = baseUrl +
                    "/rest/greenhopper/1.0/xboard/work/allData.json?rapidViewId=" + aConfig.devBoardId;
                if (aConfig.view[selectedView].dQF.length > 0) {
                    dBoardUrl += "&activeQuickFilters=" + aConfig.view[selectedView].dQF.join("&activeQuickFilters=");
                }
                dBoardDataFetch = fetch(dBoardUrl).then(res => {
                    incLoadProgress();
                    return res.ok ? res.json() : undefined;
                });
            }

            let [bBoardData, bEditModel, dEditModel, devBoard] = await Promise.all([bBoardDataFetch, bEditModelFetch, dEditModelFetch, dBoardDataFetch]);

            setLoadProgress([6, 18]);

            let newTableResult = new TableResult(bBoardData, bEditModel, dEditModel, aConfig, selectedView, priorities);

            let busToDevSearchIssues: JiraSearchIssue[] = await loadDevIssues(aConfig, newTableResult);

            newTableResult.fillSeachedIssues(busToDevSearchIssues, devBoard);

            await loadIssuesInfo(aConfig, newTableResult);

            newTableResult.finalRecalc();

            setLoadProgress([0, 0]);

            setTableResult(newTableResult);
        }

        function renderEpicCell(epic: Epic | undefined, bus: BusinessIssue) {

            const epicInfo = (epic === undefined) ? tableResult.issues[bus.epic] : tableResult.issues[epic.key];
            const epicData = epic ??
                ((epicInfo === undefined) ? undefined : tableResult.epics.find(e => e.key === epicInfo.key));

            if (epicData === undefined) {
                return <td rowSpan={bus.childCount + 1}
                           className="MainTableHeader MainTableHeaderLeft1 MainTableBG">&nbsp;</td>;
            } else {
                return <td
                    className={"MainTableEpic MainTableHeader MainTableHeaderLeft1" +
                        (epicInfo?.blocks?.length > 0 ? " MainTableBlockedIssue" : "")}
                    rowSpan={epic === undefined ? (bus.childCount + 1) : epic.childCount}
                    data-tooltip-id="IssueTooltip"
                    data-tooltip-content={epicInfo.key}
                    data-tooltip-place="right"
                >
                    <img src={epicInfo.priorityUrl} alt={epicInfo.priority} title={epicInfo.priority}
                         className="MainTablePriority"/>
                    <a
                        href={baseUrl + "/browse/" + epicInfo.key} target="_blank"
                        rel="noreferrer">{epicInfo.summary}</a>
                    <div
                        className="MainTableCounter">{epicData.business.length}+{epicData.childCount - epicData.business.length}</div>
                </td>;
            }
        }

        function renderBusinessCell(bBus: TableResultBoardEntry, bus: IssueInfo) {
            if (bBus.mappedStatuses.includes(bus.statusId)) {
                return <td
                    className={"MainTableBusinessActive" + (tableResult.issues[bus.key]?.blocks?.length > 0 ? " MainTableBlockedIssue" : "")}
                    colSpan={bBus.devColumns.length > 0 ? bBus.devColumns.length : 1}
                    data-tooltip-id="IssueTooltip"
                    data-tooltip-content={bus.key}
                >
                    <img src={bus.priorityUrl} alt={bus.priority} title={bus.priority} className="MainTablePriority"/>
                    <a href={baseUrl + "/browse/" + bus.key} target="_blank"
                       rel="noreferrer">{bus.key}</a>
                </td>;
            } else {
                return <td
                    className="MainTableBusiness"
                    colSpan={bBus.devColumns.length > 0 ? bBus.devColumns.length : 1}
                >&nbsp;</td>;
            }
        }

        function renderDevCell(bDev: TableResultBoardEntryDevColumn | undefined, dev: IssueInfo) {
            if (bDev === undefined) {
                return <td className="MainTableBG">&nbsp;</td>;
            } else {
                let isActive = bDev.mappedStatuses.includes(dev.statusId);
                if (isActive) {
                    return <td
                        className={"MainTableDevActive" + (tableResult.issues[dev.key]?.blocks?.length > 0 ? " MainTableBlockedIssue" : "")}
                        data-tooltip-id="IssueTooltip"
                        data-tooltip-content={dev.key}
                    >
                        <img src={dev.priorityUrl} alt={dev.priority} title={dev.priority}
                             className="MainTablePriority"/>
                        <a href={baseUrl + "/browse/" + dev.key} target="_blank"
                           rel="noreferrer">{dev.key}</a>
                    </td>;
                } else {
                    return <td className="MainTableDev">&nbsp;</td>;
                }
            }
        }

        function renderBusiness(epic: Epic | undefined, businessIssues: BusinessIssue[]) {

            let rows: React.JSX.Element[] = [];

            businessIssues.forEach((bus, busIndex) => {
                const busInfo = tableResult.issues[bus.key];
                rows.push(
                    <tr>
                        {(busIndex === 0) ?
                            renderEpicCell(epic, bus) : <></>}
                        <td className={"MainTableBusiness MainTableHeader MainTableCounterHolder"
                            + (busInfo?.blocks?.length > 0 ? " MainTableBlockedIssue" : "")}
                            rowSpan={bus.childCount + 1}
                            data-tooltip-id="IssueTooltip"
                            data-tooltip-content={busInfo.key}
                            data-tooltip-place="right"
                        >
                            <img src={busInfo.priorityUrl} alt={busInfo.priority} title={busInfo.priority}
                                 className="MainTablePriority"/>
                            <a
                                href={baseUrl + "/browse/" + busInfo.key} target="_blank"
                                rel="noreferrer">{busInfo.key + ": " + busInfo.summary}
                            </a>
                            <div className="MainTableCounter">{bus.childCount}</div>
                        </td>
                        <td className="MainTableHeader MainTableBusiness MainTableLastHeaderColumn">&nbsp;</td>
                        {tableResult.board.map(bBus => renderBusinessCell(bBus, busInfo))}
                    </tr>);

                bus.devIssues.forEach(dev => {
                    const devInfo = tableResult.issues[dev.key];
                    rows.push(
                        <tr>
                            <td className={"MainTableDevHeader MainTableHeader MainTableLastHeaderColumn"
                                + (tableResult.issues[devInfo.key]?.blocks?.length > 0 ? " MainTableBlockedIssue" : "")}
                                title={devInfo.summary}
                                data-tooltip-id="IssueTooltip"
                                data-tooltip-content={devInfo.key}
                                data-tooltip-place="right"
                            >
                                <a href={baseUrl + "/browse/" + devInfo.key} target="_blank"
                                   rel="noreferrer" title={devInfo.summary}>{devInfo.key}</a>
                            </td>
                            {tableResult.board
                                .flatMap(bus => bus.devColumns.length > 0 ? bus.devColumns : [undefined])
                                .map(bDev => renderDevCell(bDev, devInfo))}
                        </tr>
                    )
                });
            });

            rows.push(<tr>
                <td className="MainTableSpace" colSpan={tableResult.statusColumns + 3}></td>
            </tr>);

            return rows;
        }

        function renderDev(devIssues: DevIssue[]) {

            let rows: React.JSX.Element[] = [];
            devIssues.forEach((dev, index) => {
                const devInfo = tableResult.issues[dev.key];
                rows.push(
                    <tr>
                        {index === 0 && <td rowSpan={devIssues.length}
                                            className="MainTableHeader MainTableHeaderLeft1 MainTableBG">&nbsp;</td>}
                        {index === 0 && <td rowSpan={devIssues.length}
                                            className="MainTableHeader MainTableBG">&nbsp;</td>}
                        <td className={"MainTableDevHeader MainTableHeader MainTableLastHeaderColumn"
                            + (tableResult.issues[devInfo.key]?.blocks?.length > 0 ? " MainTableBlockedIssue" : "")}
                            title={devInfo.summary}
                            data-tooltip-id="IssueTooltip"
                            data-tooltip-content={devInfo.key}
                            data-tooltip-place="right"
                        >
                            <a href={baseUrl + "/browse/" + devInfo.key} target="_blank"
                               rel="noreferrer" title={devInfo.summary}>{devInfo.key}</a>
                        </td>
                        {
                            tableResult.board
                                .flatMap(bus => bus.devColumns.length > 0 ? bus.devColumns : [undefined])
                                .map(bDev => renderDevCell(bDev, devInfo))
                        }
                    </tr>);
            });

            if (devIssues.length > 0) {
                rows.push(<tr>
                    <td className="MainTableSpace" colSpan={tableResult.statusColumns + 3}></td>
                </tr>);
            }

            return rows;
        }


        useEffect(() => {
            loadData().then();
            // eslint-disable-next-line react-hooks/exhaustive-deps
        }, []);


        function renderData() {

            return tableResult.orderInfo.flatMap(order => [
                order.epic.map(epic => renderBusiness(epic, epic.business)),
                order.business.map(bus => renderBusiness(undefined, [bus])),
                renderDev(order.dev)
            ]);
        }

        function renderTime(diffStateTime: number) {

            const diffStateYears = Math.floor(diffStateTime / 1000 / 60 / 60 / 24 / 365);
            const diffStateDays = Math.floor((diffStateTime / 1000 / 60 / 60 / 24) % 365);
            const diffStateHours = Math.floor((diffStateTime / 1000 / 60 / 60) % 24);

            return "" +
                ((diffStateYears > 0) ? diffStateYears.toFixed(0) + " . " : "") +
                ((diffStateDays > 0) ? diffStateDays.toFixed(0) + " . " : "") +
                ((diffStateHours > 0) ? diffStateHours.toFixed(0) + " ." : "");
        }

        return <div>
            <table className="MainTable">
                <thead>
                <tr>
                    <th className="MainTableHeaderTop1Left1 MainTableLastHeaderColumn" colSpan={3}>
                        {(loadProgress[1] > 0) ?
                            <ProgressBar score={Math.floor(100 * loadProgress[0] / loadProgress[1])}/> : <></>}
                        <select key="MainTableSelectedView" value={selectedView}
                                onChange={e => setSelectedView(Number.parseInt(e.target.value))}>
                            {config.view.map((view, index) => <option value={index}>{view.name}</option>)}
                        </select><br/>
                        <button onClick={() => loadData()}> </button>
                        <button onClick={() => showConfigCallback()}></button>
                        {tableResult.errors.length > 0 ?
                            <div className="AppError">{tableResult.errors.join(", ")}</div> : <div/>}
                    </th>
                    {tableResult.board.map(bus => <th
                        className="MainTableBusiness MainTableHeaderTop1"
                        colSpan={bus.devColumns.length > 0 ? bus.devColumns.length : 1}
                    >{bus.name}
                        <div className="MainTableCounter">{bus.childCount}</div>
                    </th>)}
                </tr>
                <tr>
                    <th className="MainTableSpan MainTableHeaderTop2Left1">
                        
                        <div className="MainTableCounter">
                            {tableResult.epicCount.toString()}
                        </div>
                    </th>
                    <th className="MainTableSpan MainTableHeaderTop2">
                         
                        <div className="MainTableCounter">
                            {tableResult.businessCount.toString()}
                        </div>
                    </th>
                    <th className="MainTableSpan MainTableHeaderTop2 MainTableLastHeaderColumn">
                        
                        <div className="MainTableCounter">
                            {tableResult.devCount.toString() + (tableResult.devUnbusCount > 0 ? "+" + tableResult.devUnbusCount.toString() : "")}
                        </div>
                    </th>
                    {tableResult.board
                        .flatMap(bus => bus.devColumns.length > 0 ? bus.devColumns : [undefined])
                        .map(dev =>
                            <th className={dev === undefined ? "MainTableBG MainTableHeaderTop2" : "MainTableDev MainTableHeaderTop2"}
                            >{dev === undefined ? " " : dev.name}
                                {dev !== undefined && <div className="MainTableCounter">
                                    {dev.childCount.toString() + (dev.childDevCount > 0 ? "+" + dev.childDevCount.toString() : "")}
                                </div>}
                            </th>)}
                </tr>
                </thead>
                <tbody>
                {renderData()}
                </tbody>
            </table>
            <div className="MainTableIssueTooltipPlaceholder">
                <Tooltip key="IssueTooltip" id="IssueTooltip" place="left-start" delayShow={200} noArrow={true}
                         opacity={0.98}
                         className="MainTableIssueTooltip"
                         render={({content}) => {
                             const issue = tableResult.issues[content ? content : ""];
                             const stateTime: Date = (issue?.statusChanged > 0) ? new Date(issue?.statusChanged) : new Date();
                             const now = new Date().getTime();
                             let diffStateTime = renderTime(now - stateTime.getTime());
                             if (issue !== undefined) {
                                 return <div className="MainTableIssueTooltipInfo">
                                     <div className="IssueTooltipKey">{issue.key}</div>
                                     <div className="IssueTooltipSummary">{issue.summary}</div>
                                     <table className="IssueTooltipTable">
                                         <tbody>
                                         <tr>
                                             <td className="IssueTooltipPriority">:</td>
                                             <td className="IssueTooltipPriorityValue">{issue.priority}</td>
                                         </tr>
                                         <tr>
                                             <td className="IssueTooltipStatus">:</td>
                                             <td className="IssueTooltipStatusValue">{issue.status}
                                                 {(issue.statusChanged > 0) && <span
                                                     className="IssueTooltipStatusTime"> ({new Date(issue.statusChanged).toLocaleString(window.navigator.language)})
                                                     {(diffStateTime !== "") ? " - " + diffStateTime : ""}
                                                     </span>
                                                 }
                                             </td>
                                         </tr>
                                         <tr>
                                             <td className="IssueTooltipAssignee">:</td>
                                             <td className="IssueTooltipAssigneeValue">{issue.assigneeName}</td>
                                         </tr>
                                         <tr>
                                             <td className="IssueTooltipBlocks">:</td>
                                             <td className="IssueTooltipBlocksValue"
                                                 dangerouslySetInnerHTML={{__html: (issue.blocks?.length > 0) ? issue.blocks.join("<br/>") : ""}}/>
                                         </tr>
                                         {issue.issueType === IssueType.Bus &&
                                             <tr>
                                                 <td className="IssueTooltipAssignee">WIA Upstream:</td>
                                                 <td className="IssueTooltipAssigneeValue">{renderTime(issue.busUpstreamAge)}</td>
                                             </tr>
                                         }
                                         {issue.issueType === IssueType.Bus &&
                                             <tr>
                                                 <td className="IssueTooltipAssignee">WIA Downstream:</td>
                                                 <td className="IssueTooltipAssigneeValue">{renderTime(issue.busDownstreamAge)}</td>
                                             </tr>
                                         }
                                         </tbody>
                                     </table>
                                 </div>;
                             } else {
                                 return <div/>;
                             }
                         }}
                /></div>
        </div>;
    };