import {FC, useEffect, useState} from "react";
import {SimpleCallback} from "./CommonTypes";
import {ConfigV2, ConfigV2View, loadConfig, saveConfig} from "./Config";
import {JiraEditModel, JiraFields, JiraIssueLinkTypes, JiraQuickFilter} from "./JiraTypes";
import './ConfigEditor.css';

interface IdNameData {
    "id": string,
    "name": string
}

export const ConfigEditor: FC<{ baseUrl: string, businessBoardId: string, hideConfig: SimpleCallback }> =
    ({baseUrl, businessBoardId, hideConfig}) => {
        let defConfig = new ConfigV2();
        let [config, setConfig] = useState<ConfigV2>(defConfig);
        let [fields, setFields] = useState<JiraFields>([]);
        let [bBoard, setBBoard] = useState<IdNameData[]>([]);
        let [dBoard, setDBoard] = useState<IdNameData[]>([]);
        let [links, setLinks] = useState<IdNameData[]>([]);
        let [devBoardIdEdited, setDevBoardIdEdited] = useState<string>(defConfig.devBoardId);
        let [devBoardCorrect, setDevBoardCorrect] = useState<boolean>(false);
        let [busBoardName, setBusBoardName] = useState<string>("");
        let [devBoardName, setDevBoardName] = useState<string>("");
        let [selectedCell, setSelectedCell] = useState<{ bus: boolean, id: string } | undefined>(undefined);

        let [bBoardNames, setBBoardNames] = useState<{ [id: string]: string }>({});
        let [dBoardNames, setDBoardNames] = useState<{ [id: string]: string }>({});
        let [boardLinks, setBoardLinks] = useState<{ bus: string, devs: string[] }[]>([]);

        let [bQFilters, setBQFilters] = useState<JiraQuickFilter[]>([]);
        let [dQFilters, setDQFilters] = useState<JiraQuickFilter[]>([]);

        function compareDictionaries(d1: { [id: string]: string }, d2: { [id: string]: string }): boolean {
            // quick check for the same object
            if (d1 === d2) {
                return true;
            }

            // check for null
            if (d1 == null || d2 == null) {
                return false;
            }

            let k1 = Object.keys(d1);
            let k2 = Object.keys(d2);

            if (k1.length !== k2.length) {
                return false;
            }

            for (const k of k1) {
                if (!(k in d2)) {
                    return false;
                }
                if (d1[k] !== d2[k]) {
                    return false;
                }
            }

            return true;
        }

        function calcBoardLinks(aDBoard: IdNameData[], aBBoard: IdNameData[], aConfig: ConfigV2) {
            let dBoardIds: string[] = aDBoard.map(col => col.id);

            let bLinks: { bus: string, devs: string[] }[] = [{bus: "", devs: []}];
            aBBoard.forEach(col => bLinks.push({bus: col.id, devs: []}));

            let bIndexed = bLinks.filter(value => value.bus in aConfig.businessToDevIndexes);

            if (bIndexed.length > 0) {
                const firstDevIndex = aConfig.businessToDevIndexes[bIndexed[0].bus];
                while (dBoardIds.length > 0 && dBoardIds[0] !== firstDevIndex) {
                    bLinks[0].devs.push(dBoardIds.shift()!);
                }

                while (bIndexed.length > 1) {
                    const bus = bIndexed.shift()!;
                    const nextDevIndex = aConfig.businessToDevIndexes[bIndexed[0].bus];

                    while (dBoardIds.length > 0 && dBoardIds[0] !== nextDevIndex) {
                        bus.devs.push(dBoardIds.shift()!);
                    }
                }

                if (bIndexed.length > 0) {
                    const bus = bIndexed.shift()!;
                    dBoardIds.forEach(dev => bus.devs.push(dev));
                }
            } else {
                dBoardIds.forEach(dev => bLinks[0].devs.push(dev));
            }
            return bLinks;
        }

        function recalcBoards(aConfig: ConfigV2, aBBoard: IdNameData[], aDBoard: IdNameData[]) {
            let names: { [id: string]: string } = {};
            aBBoard.forEach(col => names[col.id] = col.name);
            setBBoardNames(names);
            names = {};
            aDBoard.forEach(col => names[col.id] = col.name);
            setDBoardNames(names);

            let bLinks = calcBoardLinks(aDBoard, aBBoard, aConfig);

            setBoardLinks(bLinks);

            let correctBusinessToDevIndexes: { [id: string]: string } = {};

            bLinks.filter(b => b.devs.length > 0).forEach(b => correctBusinessToDevIndexes[b.bus] = b.devs[0]);

            if (!compareDictionaries(correctBusinessToDevIndexes, aConfig.businessToDevIndexes)) {
                let newConfig = aConfig.clone();
                newConfig.businessToDevIndexes = correctBusinessToDevIndexes;
                setConfig(newConfig);
            }
        }

        async function loadDev(aConfig: ConfigV2, aBBoard: IdNameData[]) {

            if (aConfig?.devBoardId !== undefined && aConfig?.devBoardId !== "") {

                const dEditModelUrl = baseUrl +
                    "/rest/greenhopper/1.0/rapidviewconfig/editmodel.json?rapidViewId=" + aConfig.devBoardId;
                let dBoardFetch = fetch(dEditModelUrl).then(res => res.ok ? res.json() : undefined);

                const dBoardData = await dBoardFetch;

                if (dBoardData !== undefined) {
                    const boardData = dBoardData as JiraEditModel;
                    let aDBoard = boardData
                        .rapidListConfig
                        .mappedColumns
                        .map(col => ({"id": col.id.toString(), "name": col.name}));
                    setDBoard(aDBoard);
                    setDevBoardName(boardData.name);
                    setDevBoardCorrect(true);
                    setDQFilters(boardData.quickFilterConfig.quickFilters);
                    recalcBoards(aConfig, aBBoard, aDBoard);
                }
            }
        }

        async function loadData() {
            setDevBoardCorrect(false);

            let aConfig: ConfigV2 = await loadConfig(baseUrl, businessBoardId);
            setConfig(aConfig);

            const bEditModelUrl = baseUrl +
                "/rest/greenhopper/1.0/rapidviewconfig/editmodel.json?rapidViewId=" + businessBoardId;
            let bBoardFetch = fetch(bEditModelUrl).then(res => res.ok ? res.json() : undefined);

            const jiraConfigUrl = baseUrl + "/rest/api/2/issueLinkType";
            let jiraConfigFetch = fetch(jiraConfigUrl).then(res => res.ok ? res.json() : undefined);

            const jiraFieldsUrl = baseUrl + "/rest/api/2/field";
            let jiraFieldsFetch = fetch(jiraFieldsUrl).then(res => res.ok ? res.json() : undefined);

            const [bBoardData, jiraConfigData, jiraFieldsData] =
                await Promise.all([bBoardFetch, jiraConfigFetch, jiraFieldsFetch]);

            setFields(jiraFieldsData as JiraFields)

            const boardData = bBoardData as JiraEditModel;
            let aBBoard = boardData
                .rapidListConfig
                .mappedColumns
                .filter(col => !col.isKanPlanColumn)
                .map(col => ({"id": col.id.toString(), "name": col.name}));

            setBBoard(aBBoard);
            setBusBoardName(boardData.name);
            setBQFilters(boardData.quickFilterConfig.quickFilters);

            setLinks((jiraConfigData as JiraIssueLinkTypes).issueLinkTypes.map(link => ({
                "id": link.id.toString(),
                "name": link.name + ": " + link.outward + " -> " + link.inward
            })));
            setDevBoardIdEdited(aConfig?.devBoardId === undefined ? "" : aConfig?.devBoardId);

            await loadDev(aConfig, aBBoard);
        }

        async function saveAndExit() {
            await saveConfig(baseUrl, businessBoardId, config);
            hideConfig();
        }

        useEffect(() => {
            loadData().then();
            // eslint-disable-next-line react-hooks/exhaustive-deps
        }, []);

        function selectLink(value: string) {
            if (value !== undefined) {
                let newConfig = (config === undefined) ? new ConfigV2() : config.clone();
                newConfig.businessToDevLinkId = value;
                setConfig(newConfig);
            }
        }

        function selectBlockField(value: string) {
            if (value !== undefined) {
                let newConfig = (config === undefined) ? new ConfigV2() : config.clone();
                newConfig.blockField = value;
                setConfig(newConfig);
            }
        }

        function setUsePriority(index: number, value: boolean) {
            if (value !== undefined) {
                let newConfig = (config === undefined) ? new ConfigV2() : config.clone();
                newConfig.view[index].usePriority = value;
                setConfig(newConfig);
            }
        }

        function setShowDev(index: number, value: boolean) {
            if (value !== undefined) {
                let newConfig = (config === undefined) ? new ConfigV2() : config.clone();
                newConfig.view[index].showDev = value;
                setConfig(newConfig);
            }
        }

        async function applyDevBoard() {
            if (devBoardIdEdited !== undefined && devBoardIdEdited !== "") {
                setDevBoardCorrect(false);
                let newConfig = (config === undefined) ? new ConfigV2() : config.clone();
                newConfig.devBoardId = devBoardIdEdited;
                setConfig(newConfig);
                await loadDev(config, bBoard);
            }
        }

        function clickCell(bus: boolean, id: string) {
            if (selectedCell?.bus === bus && selectedCell?.id === id) {
                setSelectedCell(undefined);
                return;
            }
            if (selectedCell === undefined) {
                setSelectedCell({bus: bus, id: id});
                return;
            }

            if (selectedCell.bus === bus) {
                setSelectedCell({bus: bus, id: id});
                return;
            }

            const newConfig = config.clone();
            const busId = bus ? id : selectedCell.id;
            const devId = bus ? selectedCell.id : id;

            let bLinks = calcBoardLinks(dBoard, bBoard, newConfig);

            const busIdIndex = bLinks.findIndex(link => link.bus === busId)

            if (bLinks[busIdIndex].devs.includes(devId)) {
                newConfig.businessToDevIndexes[busId] = devId;
            } else {
                const index = bLinks.findIndex(link => link.devs.includes(devId));

                if (index < busIdIndex) {
                    for (let i = busIdIndex; i > index; i--) {
                        if (bLinks[i].bus in newConfig.businessToDevIndexes) {
                            delete newConfig.businessToDevIndexes[bLinks[i].bus];
                        }
                    }
                    if (bLinks[index].devs.length > 0 && bLinks[index].devs[0] === devId) {
                        delete newConfig.businessToDevIndexes[bLinks[index].bus];
                    }
                } else {
                    for (let i = busIdIndex; i < index; i++) {
                        if (bLinks[i].bus in newConfig.businessToDevIndexes) {
                            delete newConfig.businessToDevIndexes[bLinks[i].bus];
                        }
                    }
                    if (bLinks[index].devs.length > 0) {
                        let i = bLinks[index].devs.findIndex(val => val === devId) + 1;
                        if (i < bLinks[index].devs.length) {
                            newConfig.businessToDevIndexes[bLinks[index].bus] = bLinks[index].devs[i];
                        }
                    }
                }
                newConfig.businessToDevIndexes[busId] = devId;
            }
            setConfig(newConfig);
            setSelectedCell(undefined);
            recalcBoards(newConfig, bBoard, dBoard);
        }

        function setBSelQFiltersValue(index: number, id: string, value: boolean) {
            if (value) {
                if (!config.view[index].bQF.includes(id)) {
                    let newConfig = config.clone();
                    newConfig.view[index].bQF = [...config.view[index].bQF, id];
                    setConfig(newConfig);
                }
            } else {
                if (config.view[index].bQF.includes(id)) {
                    let newConfig = config.clone();
                    newConfig.view[index].bQF = config.view[index].bQF.filter(i => i !== id);
                    setConfig(newConfig);
                }
            }
        }

        function setDSelQFiltersValue(index: number, id: string, value: boolean) {
            if (value) {
                if (!config.view[index].dQF.includes(id)) {
                    let newConfig = config.clone();
                    newConfig.view[index].dQF = [...config.view[index].dQF, id];
                    setConfig(newConfig);
                }
            } else {
                if (config.view[index].dQF.includes(id)) {
                    let newConfig = config.clone();
                    newConfig.view[index].dQF = config.view[index].dQF.filter(i => i !== id);
                    setConfig(newConfig);
                }
            }
        }

        function addView() {
            let newConfig = config.clone();

            let configV2View = new ConfigV2View();
            configV2View.name = " " + (newConfig.view.length + 1);
            newConfig.view.push(configV2View);

            setConfig(newConfig);
        }

        function renameView(index: number, value: string) {
            let newConfig = config.clone();
            newConfig.view[index].name = value;
            setConfig(newConfig);
        }

        function removeView(index: number) {
            let newConfig = config.clone();
            delete newConfig.view[index];
            setConfig(newConfig);
        }

        function setUpstreamSort(index: number, order: string) {
            let newConfig = config.clone();
            newConfig.view[index].upstreamSort = order;
            setConfig(newConfig);
        }

        function setDownstreamSort(index: number, order: string) {
            let newConfig = config.clone();
            newConfig.view[index].downstreamSort = order;
            setConfig(newConfig);
        }

        function setDownstreamId(value: string) {
            if (value !== undefined) {
                let newConfig = config.clone();
                newConfig.downstreamId = value;
                setConfig(newConfig);
            }
        }

        return <div>
            <button key="configEditorReload" onClick={() => loadData()}> </button>
            &nbsp;
            <button key="configEditorSave" onClick={() => saveAndExit()}>   </button>
            &nbsp;
            <button key="configEditorCancel" onClick={() => hideConfig()}>  </button>
            <div className="ConfigDiv">
                <div className="ConfigEditorBlock">
                    <div className="ConfigEditorBlockName">
                         
                    </div>
                    <div className="ConfigEditorPart">
                        <span className="ConfigEditorTitle">   <br/></span>
                        <select key="ConfigDownstreamCol"
                                value={config.downstreamId}
                                onChange={(e) => setDownstreamId(e.target.value)}
                        >
                            {bBoard.map(col =>
                                <option value={col.id} key={"downstreamcol_" + col.id}>{col.name}</option>)}
                        </select><br/>
                    </div>

                    <div className="ConfigEditorPart">
                        <span className="ConfigEditorTitle">   Checklist,     <br/></span>
                        <select key="CongigLinkSelect" onChange={(e) => selectBlockField(e.target.value)}
                                value={config?.blockField}
                        >
                            <option value={undefined}>-----      -----</option>
                            {fields.filter(field => field?.schema?.custom === "com.okapya.jira.checklist:checklist").map(field =>
                                <option value={field.id} key={"blockfield_" + field.id}>{field.name}</option>)}
                        </select>
                    </div>
                    <div className="ConfigEditorPart">
                        <span
                            className="ConfigEditorTitle">       <br/></span>
                        <select key="CongigLinkSelect" onChange={(e) => selectLink(e.target.value)}
                                value={config?.businessToDevLinkId}
                        >
                            <option value={undefined}>-----   -----</option>
                            {links.map(link =>
                                <option value={link.id} key={"link_" + link.id}>{link.name}</option>)}
                        </select>
                    </div>
                    <div className="ConfigEditorPart">
                    <span className="ConfigEditorTitle">  :&nbsp;
                    </span><input type="text"
                                  onChange={(e) => setDevBoardIdEdited(e.target.value)}
                                  maxLength={10}
                                  value={devBoardIdEdited}
                                  key="ConfigEditorDevTableId"/>
                        <button onClick={() => applyDevBoard()}> </button>
                        <br/>
                        <span className="ConfigEditorHint">        " ".<br/>
                      URL jira   https://jira.company.com/secure/RapidBoard.jspa?rapidView=<strong>905</strong></span>
                    </div>
                </div>
                {devBoardCorrect &&
                    <div className="ConfigEditorPart">
                        <span className="ConfigEditorTitle"> : "{busBoardName}" -&gt; "{devBoardName}"</span>
                        <table className="ConfigEditorLinkTable">
                            <tbody>
                            <tr>
                                {boardLinks[0].devs.length > 0 && <td colSpan={boardLinks[0].devs.length}></td>}

                                {boardLinks.filter(col => col.bus !== "").map(col => (
                                    <td className={(selectedCell?.bus && selectedCell?.id === col.bus) ? "ConfigEditorLinkTableSelected" : ""}
                                        colSpan={col.devs.length > 0 ? col.devs.length : 1}
                                        onClick={() => clickCell(true, col.bus)}
                                        key={"link_bus_" + col.bus}>{bBoardNames[col.bus]}</td>))}
                            </tr>
                            <tr>
                                {boardLinks[0].devs.length > 0 && boardLinks[0].devs.map(dev => (
                                    <td className={(!selectedCell?.bus && selectedCell?.id === dev) ? "ConfigEditorLinkTableSelected" : ""}
                                        onClick={() => clickCell(false, dev)}
                                        key={"link_bus_" + dev}>{dBoardNames[dev]}</td>
                                ))}
                                {boardLinks.filter(col => col.bus !== "").flatMap(bus => {
                                    if (bus.devs.length > 0) {
                                        return bus.devs.map(dev => (
                                            <td className={(!selectedCell?.bus && selectedCell?.id === dev) ? "ConfigEditorLinkTableSelected" : ""}
                                                onClick={() => clickCell(false, dev)}
                                                key={"link_bus_" + dev}>{dBoardNames[dev]}</td>))
                                    } else {
                                        return <td>&nbsp;</td>
                                    }
                                })
                                }
                            </tr>
                            </tbody>
                        </table>
                    </div>
                }
                <button key="AddView" id="AddView" onClick={() => addView()}> </button>
                <br/>
                {
                    config.view.map((view, index) =>
                        <div className="ConfigEditorBlock">
                            <div className="ConfigEditorBlockName">
                                 : <input type="text" value={view.name}
                                                       onChange={e => renameView(index, e.target.value)}/>
                                <button onClick={() => removeView(index)}> </button>
                            </div>
                            <div className="ConfigEditorPart">
                                <input type="checkbox"
                                       key={"ConfigCombineEpics-" + index.toString()}
                                       checked={view.combineEpics}
                                       id={"ConfigCombineEpics-" + index.toString()}
                                       onChange={e => setUsePriority(index, e.target.checked)}
                                />
                                <label htmlFor={"ConfigCombineEpics-" + index.toString()} className="ConfigEditorTitle">
                                    &nbsp; -  </label><br/>

                                <span className="ConfigEditorBlockSpace"></span><br/>
                                <input type="checkbox" key={"ConfigUsePriority-" + index.toString()}
                                       checked={view.usePriority}
                                       id={"ConfigUsePriority-" + index.toString()}
                                       onChange={e => setUsePriority(index, e.target.checked)}
                                />
                                <label htmlFor={"ConfigUsePriority-" + index.toString()}
                                       className="ConfigEditorTitle">
                                    &nbsp;    </label><br/>

                                <span className="ConfigEditorBlockSpace"></span><br/>
                                <span className="ConfigEditorTitle">      <br/></span>
                                <input type="radio" id={"BusSortUR-" + index.toString()}
                                       checked={view.upstreamSort === "r"}
                                       onChange={() => setUpstreamSort(index, "r")}
                                       name={"BusSortU-" + index.toString()} value="r"/>
                                <label htmlFor={"BusSortUR-" + index.toString()}>  </label><br/>
                                <input type="radio" id={"BusSortUA-" + index.toString()}
                                       checked={view.upstreamSort === "a"}
                                       onChange={() => setUpstreamSort(index, "a")}
                                       name={"BusSortU-" + index.toString()} value="a"/>
                                <label htmlFor={"BusSortUA-" + index.toString()}>   </label><br/>

                                <span className="ConfigEditorBlockSpace"></span><br/>
                                <span
                                    className="ConfigEditorTitle">      <br/></span>
                                <input type="radio" id={"BusSortDR-" + index.toString()}
                                       checked={view.downstreamSort === "r"}
                                       onChange={() => setDownstreamSort(index, "r")}
                                       name={"BusSortD-" + index.toString()} value="r"/>
                                <label htmlFor={"BusSortDR-" + index.toString()}>  </label><br/>
                                <input type="radio" id={"BusSortDA-" + index.toString()}
                                       checked={view.downstreamSort === "a"}
                                       onChange={() => setDownstreamSort(index, "a")}
                                       name={"BusSortD-" + index.toString()} value="a"/>
                                <label htmlFor={"BusSortDA-" + index.toString()}>   </label><br/>

                                <span className="ConfigEditorBlockSpace"></span><br/>
                                <input type="checkbox" key={"ConfigShowDev-" + index.toString()}
                                       checked={view.showDev}
                                       id={"ConfigShowDev-" + index.toString()}
                                       title=" ,       "
                                       onChange={e => setShowDev(index, e.target.checked)}
                                />
                                <label htmlFor={"ConfigShowDev-" + index.toString()} className="ConfigEditorTitle"
                                       title=" ,       ">&nbsp;
                                      ,     </label>
                            </div>
                            <div className="ConfigEditorPart">
                                <div className="ConfigEditorTitle">  </div>
                                {bQFilters.map(qf =>
                                    <div>
                                        <input type="checkbox" key={"ConfigBQF-" + index.toString() + "-" + qf.id}
                                               id={"ConfigBQF-" + index.toString() + "-" + qf.id}
                                               checked={view.bQF.includes(qf.id.toString())}
                                               onChange={e => setBSelQFiltersValue(index, qf.id.toString(), e.target.checked)}
                                        />
                                        <label
                                            htmlFor={"ConfigBQF-" + index.toString() + "-" + qf.id}>&nbsp;{qf.name}</label>
                                    </div>)}
                            </div>
                            <div className="ConfigEditorPart">
                                <div className="ConfigEditorTitle">  </div>
                                {dQFilters.map(qf =>
                                    <div>
                                        <input type="checkbox" key={"ConfigDQF-" + index.toString() + "-" + qf.id}
                                               id={"ConfigDQF-" + index.toString() + "-" + qf.id}
                                               checked={view.dQF.includes(qf.id.toString())}
                                               onChange={e => setDSelQFiltersValue(index, qf.id.toString(), e.target.checked)}
                                        />
                                        <label
                                            htmlFor={"ConfigDQF-" + index.toString() + "-" + qf.id}>&nbsp;{qf.name}</label>
                                    </div>)}
                            </div>
                        </div>
                    )
                }
            </div>
        </div>;
    }