export class ConfigV1 {
    devBoardId: string = "";
    businessToDevIndexes: { [busIndex: string]: string } = {};
    businessToDevLinkId: string = "";
    blockField: string = "";
    usePriority: boolean = false;
    showDev: boolean = false;
    bQF: string[] = [];
    dQF: string[] = [];

    clone(): ConfigV1 {

        let res = new ConfigV1();

        res.devBoardId = this.devBoardId;

        Object.keys(this.businessToDevIndexes).forEach(i => res.businessToDevIndexes[i] = this.businessToDevIndexes[i]);
        res.businessToDevLinkId = this.businessToDevLinkId;
        res.blockField = this.blockField;
        res.usePriority = this.usePriority;
        res.showDev = this.showDev;
        res.bQF = [...this.bQF];
        res.dQF = [...this.dQF];

        return res;
    }
}

export class ConfigV2View {
    name: string = " 1";
    bQF: string[] = [];
    dQF: string[] = [];
    usePriority: boolean = false;
    showDev: boolean = false;
    combineEpics: boolean = false;
    upstreamSort: string = "r";
    downstreamSort: string = "r";


    clone(): ConfigV2View {
        let res = new ConfigV2View()

        res.name = this.name;
        res.bQF = [...this.bQF];
        res.dQF = [...this.dQF];
        res.usePriority = this.usePriority;
        res.showDev = this.showDev;
        res.combineEpics = this.combineEpics;
        res.upstreamSort = this.upstreamSort;
        res.downstreamSort = this.downstreamSort;

        return res;
    }
}

export class ConfigV2 {
    devBoardId: string = "";
    businessToDevIndexes: { [busIndex: string]: string } = {};
    businessToDevLinkId: string = "";
    blockField: string = "";
    downstreamId: string = "";
    view: ConfigV2View[] = [new ConfigV2View()];

    clone(): ConfigV2 {

        let res = new ConfigV2();

        res.devBoardId = this.devBoardId;
        Object.keys(this.businessToDevIndexes).forEach(i => res.businessToDevIndexes[i] = this.businessToDevIndexes[i]);
        res.businessToDevLinkId = this.businessToDevLinkId;
        res.blockField = this.blockField;
        res.downstreamId = this.downstreamId;
        res.view = this.view.map(i => i.clone());

        return res;
    }

    static makeFromV1(v1: ConfigV1) {
        let res = new ConfigV2();
        res.devBoardId = v1.devBoardId;
        res.blockField = v1.blockField;
        res.businessToDevLinkId = v1.businessToDevLinkId;
        res.businessToDevIndexes = structuredClone(v1.businessToDevIndexes);

        let view = new ConfigV2View();
        view.name = " 1";
        view.combineEpics = true;
        view.bQF = structuredClone(v1.bQF);
        view.dQF = structuredClone(v1.dQF);
        view.showDev = v1.showDev;
        view.usePriority = v1.usePriority;

        res.view = [view];

        return res;
    }
}

export interface ConfigPropertyV1 {
    key: string;
    value: ConfigV1;
}

export interface ConfigPropertyV2 {
    key: string;
    value: ConfigV2;
}

export async function saveConfig(baseUrl: string, businessBoardId: string, config: ConfigV2) {
    let configUrl = baseUrl + "/rest/agile/1.0/board/" + businessBoardId + "/properties/jira.goose.config.v2";
    await fetch(configUrl, {
        method: 'PUT',
        headers: {
            'Content-type': 'application/json'
        },
        body: JSON.stringify(config),
    }).catch(reason => console.warn("Can't save config to board: " + reason));
}

export async function loadConfig(baseUrl: string, businessBoardId: string) {

    let configUrl = baseUrl + "/rest/agile/1.0/board/" + businessBoardId + "/properties/jira.goose.config.v2";
    let configFetch = fetch(configUrl).then(res => res.ok ? res.json() : undefined);

    let configData = await configFetch;

    let aConfig: ConfigV2;

    if (configData === undefined) {
        configUrl = baseUrl + "/rest/agile/1.0/board/" + businessBoardId + "/properties/jira.goose.config.v1";
        configFetch = fetch(configUrl).then(res => res.ok ? res.json() : undefined);

        configData = await configFetch;

        if (configData === undefined) {
            aConfig = new ConfigV2();
        } else {
            aConfig = ConfigV2.makeFromV1(Object.assign(new ConfigV1(), (configData as ConfigPropertyV1)?.value));
        }
    } else {
        aConfig = Object.assign(new ConfigV2(), (configData as ConfigPropertyV2)?.value);
    }

    aConfig.view = aConfig.view.map(v => Object.assign(new ConfigV2View(), v));

    return aConfig;
}

