import {JiraAllData, JiraBlocklist, JiraEditModel, JiraSearchIssue} from "./JiraTypes";
import {ConfigV2, ConfigV2View} from "./Config";
import {JiraOrderedPriority} from "./CommonTypes";

export interface TableResultBoardEntryDevColumn {
    id: string;
    name: string;
    childCount: number;
    childDevCount: number;
    statusGroup: StatusGroup;
    mappedStatuses: string[];
}

export interface TableResultBoardEntry {
    id: string;
    name: string;
    mappedStatuses: string[];
    childCount: number;
    statusGroup: StatusGroup;
    devColumns: TableResultBoardEntryDevColumn[];
}

interface IssueKey {
    key: string;
}

interface IssueOrderInfo extends IssueKey {
    orderValue: number;
    priorityOrder: number;
    statusGroup: StatusGroup;
    issueType: IssueType;
    busUpstreamAge: number;
    busDownstreamAge: number;
    createdTime: number;
}

interface IssueStatusChangeHistory {
    time: number;
    fromStatus: string;
    fromColumn: string;
    toStatus: string;
    toColumn: string;
}

export enum StatusGroup {
    Backlog,
    Upstream,
    Downstream,
    Ready
}

export enum IssueType {
    Epic,
    Bus,
    Dev
}

export interface IssueInfo extends IssueOrderInfo {
    id: string;
    summary: string;
    priority: string;
    priorityColor: string;
    priorityUrl: string;
    status: string;
    statusId: string;
    statusChanged: number;
    assigneeName: string;
    assigneeActive: boolean;
    issueLoaded: boolean;
    blocks: string[];
    issueStatusChangeHistory: IssueStatusChangeHistory[];
}


export interface Epic extends IssueKey {
    business: BusinessIssue[];
    childCount: number;
}

export interface BusinessIssue extends IssueKey {
    devIssues: DevIssue[];
    childCount: number;
    columnPos: number;
    epic: string;
}

export interface DevIssue extends IssueKey {
    columnPos: number;
}

export interface OrderedInfo {
    issuesType: IssueType;
    epic: Epic[],
    business: BusinessIssue[],
    dev: DevIssue[]
}

const tooLateTime = 24 * 60 * 60 * 1000;

export class TableResult {

    errors: string[] = [];
    board: TableResultBoardEntry[] = [];
    epics: Epic[] = [];
    business: BusinessIssue[] = [];
    dev: DevIssue[] = [];
    statusColumns: number = 0;
    private devBoard: TableResultBoardEntryDevColumn[] = [];
    issues: { [key: string]: IssueInfo } = {};
    private statuses: { [key: string]: string } = {};
    priorities: { [p: string]: JiraOrderedPriority } = {};
    orderInfo: OrderedInfo[] = [];
    private config: ConfigV2 = new ConfigV2();
    private readonly selectedView: number = 0;
    epicCount: number = 0;
    businessCount: number = 0;
    devCount: number = 0;
    devUnbusCount: number = 0;

    constructor(
        businessBoard: JiraAllData | undefined,
        businessEditModel: JiraEditModel | undefined,
        devEditModel: JiraEditModel | undefined,
        config: ConfigV2 | undefined,
        selectedView: number,
        aPriorities: { [p: string]: JiraOrderedPriority }
    ) {

        this.priorities = aPriorities;

        if (
            businessBoard === undefined ||
            businessEditModel === undefined ||
            devEditModel === undefined ||
            config === undefined
        ) {
            return;
        }

        this.config = config;
        this.selectedView = selectedView;

        for (let statusKey in businessBoard.entityData.statuses) {
            this.statuses[statusKey] = businessBoard.entityData.statuses[statusKey].statusName;
        }
        devEditModel.rapidListConfig.mappedColumns.forEach(col => col.mappedStatuses.forEach(st => this.statuses[st.id] = st.name));
        devEditModel.rapidListConfig.unmappedStatuses.forEach(st => this.statuses[st.id] = st.name);

        let {bBoard, dBoard} = this.fillBoards(businessEditModel, devEditModel);

        if (!this.checkBoardChanges(bBoard, dBoard) || !this.buildBoardsLinks(bBoard, dBoard)) {
            return;
        }

        this.board = bBoard;

        let epics = this.getEpics(businessBoard);
        let business: BusinessIssue[] = [];
        let epicsLinks = this.fillBusiness(businessBoard, epics, business);

        this.devBoard = dBoard;
        this.epics = epicsLinks
        this.business = business;
        this.statusColumns = bBoard
            .map(bus => (bus.devColumns.length > 0) ? bus.devColumns.length : 1)
            .reduce((accumulator, currentValue) => accumulator + currentValue, 0);
    }

    private fillNonBusinessDev(devBoardData: JiraAllData) {
        devBoardData?.issuesData?.issues?.filter(dev => !(dev.key in this.issues)).forEach(dev => {
            let devIssue: IssueInfo = {
                id: dev.id,
                key: dev.key,
                summary: dev.summary,
                status: "",
                statusId: dev.statusId,
                statusChanged: -1,
                priority: this.priorities[dev.priorityId].name,
                priorityColor: this.priorities[dev.priorityId].statusColor,
                priorityUrl: this.priorities[dev.priorityId].iconUrl,
                priorityOrder: this.priorities[dev.priorityId].order,
                orderValue: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.statusId)),
                assigneeName: "",
                assigneeActive: true,
                issueLoaded: false,
                blocks: [],
                issueType: IssueType.Dev,
                createdTime: -1,
                busUpstreamAge: -1,
                busDownstreamAge: -1,
                statusGroup: StatusGroup.Backlog,
                issueStatusChangeHistory: []
            };
            this.issues[devIssue.key] = devIssue;
            this.dev.push({
                key: devIssue.key,
                columnPos: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.statusId))
            });
        });
    }

    private fillBusiness(businessBoard: JiraAllData,
                         epicInfos: { [p: string]: IssueInfo },
                         business: BusinessIssue[]) {


        let epicLinks: { [key: string]: Epic } = {};
        let usedEpics: { [key: string]: IssueInfo } = {};

        businessBoard.issuesData.issues.forEach(businessIssue => {

            const item: IssueInfo = Object.assign(businessIssue, {
                childCount: 0,
                priority: this.priorities[businessIssue.priorityId].name,
                priorityId: businessIssue.priorityId,
                priorityUrl: this.priorities[businessIssue.priorityId].iconUrl,
                priorityColor: this.priorities[businessIssue.priorityId].statusColor,
                priorityOrder: this.priorities[businessIssue.priorityId].order,
                orderValue: this.board.findIndex(bus => bus.mappedStatuses.includes(businessIssue.statusId)),
                status: this.statuses[businessIssue.statusId],
                statusChanged: businessIssue.timeInColumn.enteredStatus,
                assigneeActive: true,
                issueLoaded: false,
                blocks: [],
                issueType: IssueType.Bus,
                createdTime: -1,
                busUpstreamAge: -1,
                busDownstreamAge: -1,
                statusGroup: StatusGroup.Backlog,
                issueStatusChangeHistory: []
            });
            this.issues[item.key] = item;

            if (businessIssue.epic !== undefined && businessIssue.epic in epicInfos) {

                if (!(businessIssue.epic in usedEpics)) {
                    usedEpics[businessIssue.epic] = epicInfos[businessIssue.epic];
                    epicLinks[businessIssue.epic] = {
                        key: businessIssue.epic,
                        business: [],
                        childCount: 0
                    };
                    this.issues[businessIssue.epic] = epicInfos[businessIssue.epic];
                }

                epicLinks[businessIssue.epic].business.push({
                    key: item.key,
                    devIssues: [],
                    columnPos: this.board.findIndex(bus => bus.mappedStatuses.includes(businessIssue.statusId)),
                    childCount: 0,
                    epic: businessIssue.epic
                });
            } else {
                business.push({
                    key: item.key,
                    devIssues: [],
                    columnPos: this.board.findIndex(bus => bus.mappedStatuses.includes(businessIssue.statusId)),
                    childCount: 0,
                    epic: ""
                });
            }
        });

        return Object.values(epicLinks);
    }

    private getEpics(businessBoard: JiraAllData) {
        let epics: { [key: string]: IssueInfo } = {};
        Object.entries(businessBoard.entityData.epics).forEach(value => epics[value[1].epicField.epicKey] = {
            id: value[1].epicField.id,
            key: value[1].epicField.epicKey,
            summary: value[1].epicField.text,
            priorityOrder: Number.MAX_VALUE,
            orderValue: 0,
            priority: "",
            priorityUrl: "",
            priorityColor: "",
            status: "",
            statusId: "",
            statusChanged: -1,
            assigneeName: "",
            assigneeActive: true,
            issueLoaded: false,
            blocks: [],
            issueType: IssueType.Epic,
            createdTime: -1,
            busUpstreamAge: -1,
            busDownstreamAge: -1,
            statusGroup: StatusGroup.Backlog,
            issueStatusChangeHistory: []
        });

        return epics;
    }

    private fillBoards(businessBoard: JiraEditModel, devEditModel: JiraEditModel) {
        let bBoard: TableResultBoardEntry[] = businessBoard.rapidListConfig.mappedColumns.map(value => (
            {
                id: value.id.toString(),
                name: value.name,
                mappedStatuses: value.mappedStatuses.map(v => v.id.toString()),
                childCount: 0,
                statusGroup: value.isKanPlanColumn ? StatusGroup.Backlog :
                    (this.config.downstreamId === value.id.toString() ? StatusGroup.Downstream : StatusGroup.Upstream),
                devColumns: []
            }
        ));
        let b = false;
        for (let i = 0; i < bBoard.length - 1; i++) {
            if (b) {
                bBoard[i].statusGroup = StatusGroup.Downstream;
            } else if (bBoard[i].statusGroup === StatusGroup.Downstream) {
                b = true;
            }
        }
        bBoard[bBoard.length - 1].statusGroup = StatusGroup.Ready;

        let dBoard: TableResultBoardEntryDevColumn[] = devEditModel.rapidListConfig.mappedColumns.map(value => (
            {
                id: value.id.toString(),
                name: value.name,
                childCount: 0,
                childDevCount: 0,
                statusGroup: value.isKanPlanColumn ? StatusGroup.Backlog : StatusGroup.Downstream,
                mappedStatuses: value.mappedStatuses.map(v => v.id.toString())
            }
        ));
        dBoard[dBoard.length - 1].statusGroup = StatusGroup.Ready;

        return {bBoard, dBoard};
    }

    private checkBoardChanges(bBoard: TableResultBoardEntry[], dBoard: TableResultBoardEntryDevColumn[]): boolean {
        let ids = bBoard.map(v => v.id);
        if (Object.keys(this.config.businessToDevIndexes).filter(id => !ids.includes(id)).length > 0) {
            this.errors.push(" :  ");
            return false;
        }

        ids = dBoard.map(v => v.id);
        if (Object.values(this.config.businessToDevIndexes).filter(id => !ids.includes(id)).length > 0) {
            this.errors.push(" :  ");
            return false;
        }
        return true;
    }

    private buildBoardsLinks(bBoard: TableResultBoardEntry[], dBoardOrig: TableResultBoardEntryDevColumn[]): boolean {

        let dBoard: TableResultBoardEntryDevColumn[] = [];

        dBoard.push(...dBoardOrig);

        let bIndexed = bBoard.filter(value => value.id in this.config.businessToDevIndexes);

        // Skip first dev columns
        const firstDevIndex = this.config.businessToDevIndexes[bIndexed[0].id];
        while (dBoard.length > 0 && dBoard[0].id !== firstDevIndex) {
            dBoard.shift();
        }

        while (bIndexed.length > 1) {
            if (dBoard.length === 0) {
                this.errors.push(" :  ");
                return false;
            }

            const bus = bIndexed.shift()!;
            const nextDevIndex = this.config.businessToDevIndexes[bIndexed[0].id];

            while (dBoard.length > 0 && dBoard[0].id !== nextDevIndex) {
                bus.devColumns.push(dBoard.shift()!);
            }
        }

        if (bIndexed.length > 0) {
            if (dBoard.length === 0) {
                this.errors.push(" :  ");
                return false;
            }
            const bus = bIndexed.shift()!;
            bus.devColumns.push(...dBoard);
        }

        return true;
    }

    fillSeachedIssues(busToDevSearchIssues: JiraSearchIssue[],
                      devBoardData: JiraAllData | undefined) {

        let busIssues: { [key: string]: BusinessIssue } = {};
        this.business.forEach(b => busIssues[b.key] = b);
        this.epics.forEach(epic => epic.business.forEach(b => busIssues[b.key] = b));

        busToDevSearchIssues.forEach(bus => {
            if (bus.key in busIssues) {
                bus.fields.issuelinks?.filter(link => link.type.id === this.config.businessToDevLinkId).map(link => link.outwardIssue)
                    .forEach(dev => {
                        let devIssue: IssueInfo = {
                            id: dev.id,
                            key: dev.key,
                            summary: dev.fields.summary,
                            status: dev.fields.status.name,
                            statusId: dev.fields.status.id,
                            statusChanged: -1,
                            priority: this.priorities[dev.fields.priority.id].name,
                            priorityColor: this.priorities[dev.fields.priority.id].statusColor,
                            priorityUrl: this.priorities[dev.fields.priority.id].iconUrl,
                            priorityOrder: this.priorities[dev.fields.priority.id].order,
                            orderValue: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.fields.status.id)),
                            assigneeName: "",
                            assigneeActive: true,
                            issueLoaded: false,
                            blocks: [],
                            issueType: IssueType.Dev,
                            createdTime: -1,
                            busUpstreamAge: -1,
                            busDownstreamAge: -1,
                            statusGroup: StatusGroup.Backlog,
                            issueStatusChangeHistory: []
                        };
                        this.issues[devIssue.key] = devIssue;

                        busIssues[bus.key].devIssues.push({
                            key: devIssue.key,
                            columnPos: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.fields.status.id))
                        });

                    });
                this.fillIssueFromSearchData(bus);
            }
        });

        Object.values(busIssues).forEach(bus => bus.childCount = bus.devIssues.length);
        this.epics.forEach(epic => {
            epic.childCount = 0;
            epic.business.forEach(bus => epic.childCount += bus.childCount + 1);
        });

        if (this.config.view[this.selectedView].showDev && devBoardData !== undefined) {
            this.fillNonBusinessDev(devBoardData);
        }
    }

    finalRecalc() {

        this.reorderData();

        this.calcStatusCounts();
    }

    private calcBusinessAges(issueInfo: IssueInfo) {
        let dInTime = -1;
        let dOutTime = -1;
        let uInTime = -1;
        let uOutTime = -1;

        issueInfo.issueStatusChangeHistory.forEach(h => {
            if (h.toColumn !== "") {
                let col = this.board.findIndex(col => col.id === h.toColumn);
                if (col >= 0) {
                    const newOrderStatus = this.board[col].statusGroup;
                    if (newOrderStatus !== issueInfo.statusGroup) {
                        if (issueInfo.statusGroup === StatusGroup.Backlog) {
                            if (newOrderStatus === StatusGroup.Upstream && h.time - uOutTime > tooLateTime) {
                                uInTime = h.time;
                            }
                        }
                        if (newOrderStatus === StatusGroup.Downstream &&
                            h.time - dOutTime > tooLateTime) {
                            if (issueInfo.statusGroup === StatusGroup.Backlog ||
                                issueInfo.statusGroup === StatusGroup.Upstream) {
                                dInTime = h.time;
                            }
                        }

                        if (issueInfo.statusGroup === StatusGroup.Upstream) {
                            uOutTime = h.time;
                        }
                        if (issueInfo.statusGroup === StatusGroup.Downstream) {
                            dOutTime = h.time;
                        }

                        issueInfo.statusGroup = newOrderStatus;
                    }
                }
            }
        });

        switch (issueInfo.statusGroup) {
            case StatusGroup.Upstream:
                issueInfo.busUpstreamAge = new Date().getTime() - uInTime;
                issueInfo.busDownstreamAge = 0;
                break;
            case StatusGroup.Downstream:
                issueInfo.busUpstreamAge = uOutTime - uInTime;
                issueInfo.busDownstreamAge = new Date().getTime() - dInTime;
                break;
            case StatusGroup.Ready:
                issueInfo.busUpstreamAge = uOutTime - uInTime;
                issueInfo.busDownstreamAge = dOutTime - dInTime;
                break;

            default:
                issueInfo.busUpstreamAge = 0;
                issueInfo.busDownstreamAge = 0;
                break;
        }

    }

    private calcIssueTransitions(issueInfo: IssueInfo, issue: JiraSearchIssue) {


        if (issue.changelog?.histories !== undefined) {
            switch (issueInfo.issueType) {
                case IssueType.Bus:
                    issueInfo.issueStatusChangeHistory = issue.changelog.histories
                        .flatMap(h => h.items.map(i => ({time: Date.parse(h.created), item: i})))
                        .filter(i => i.item.field === "status" && i.item.fieldtype === "jira")
                        .map(i => ({
                            time: i.time,
                            fromStatus: i.item.from ?? "",
                            fromColumn: this.board.find(c => c.mappedStatuses.includes(i.item.from ?? ""))?.id ?? "",
                            toStatus: i.item.from ?? "",
                            toColumn: this.board.find(c => c.mappedStatuses.includes(i.item.to ?? ""))?.id ?? ""
                        })).sort((a, b) => a.time - b.time)
                    this.calcBusinessAges(issueInfo);
                    break;
            }
        }
    }


    private fillIssueFromSearchData(issue: JiraSearchIssue) {


        const issueInfo = this.issues[issue.key];
        if (issueInfo === undefined || issueInfo.issueLoaded) {
            return;
        }

        const fields = issue.fields;
        if (fields.assignee?.displayName !== undefined) {
            issueInfo.assigneeName = fields.assignee?.displayName;
        }
        if (fields.assignee?.active !== undefined) {
            issueInfo.assigneeActive = fields.assignee?.active;
        }
        if (fields.priority?.name !== undefined) {
            issueInfo.priority = fields.priority?.name;
        }
        if (fields.priority?.id !== undefined) {
            issueInfo.priorityOrder = this.priorities[fields.priority?.id].order;
        }
        if (fields.priority?.iconUrl !== undefined) {
            issueInfo.priorityUrl = fields.priority?.iconUrl;
        }
        if (fields.status?.name !== undefined) {
            issueInfo.status = fields.status?.name;
        }
        if (fields.created !== undefined) {
            issueInfo.createdTime = Date.parse(fields.created);
        }
        if (this.config.blockField !== undefined &&
            this.config.blockField !== "" &&
            Object.hasOwn(fields, this.config.blockField)) {
            const block: JiraBlocklist[] = fields[this.config.blockField] as JiraBlocklist[];
            if (block !== undefined) {
                block.filter(block => !block.checked).forEach(block =>
                    issueInfo.blocks.push(block.name));
            }
        }
        issueInfo.issueLoaded = true;

        this.calcIssueTransitions(issueInfo, issue);
    }

    fillIssues(issues: JiraSearchIssue[]) {
        issues.forEach(issue => this.fillIssueFromSearchData(issue));
    }

    findIssuesToLoad(): string[] {

        return Object.keys(this.issues).filter(issue => !this.issues[issue].issueLoaded);
    }

    private calcStatusCounts() {

        this.epicCount = this.epics.length;
        this.epics.forEach(epic => {
            epic.business.forEach(bus => {
                this.board[bus.columnPos].childCount++;
                this.devCount += bus.devIssues.length;
            });
            this.businessCount += epic.business.length;
        });
        this.business.forEach(bus => {
            this.board[bus.columnPos].childCount++;
            this.businessCount++;
        });

        this.epics.forEach(epic => epic.business.forEach(bus => bus.devIssues.forEach(dev => {
            if (dev.columnPos >= 0) {
                this.devBoard[dev.columnPos].childCount++
            }
        })));
        this.business.forEach(bus => bus.devIssues.forEach(dev => {
            if (dev.columnPos >= 0) {
                this.devBoard[dev.columnPos].childCount++
            }
        }));
        this.dev.forEach(dev => {
            if (dev.columnPos >= 0) {
                this.devBoard[dev.columnPos].childDevCount++
            }
        });

        this.devUnbusCount += this.dev.length;

    }

    reorderData() {

        function compareIssues<K extends keyof IssueOrderInfo>(a: IssueOrderInfo, b: IssueOrderInfo, props: {
            prop: K,
            asc: boolean
        }[]): number {

            for (let i = 0; i < props.length - 1; i++) {
                if (a[props[i].prop] !== b[props[i].prop]) {
                    if (props[i].asc) {
                        return a[props[i].prop] < b[props[i].prop] ? -1 : 1;
                    } else {
                        return a[props[i].prop] < b[props[i].prop] ? 1 : -1;
                    }
                }
            }
            const i = props.length - 1;
            if (a[props[i].prop] !== b[props[i].prop]) {
                if (props[i].asc) {
                    return a[props[i].prop] < b[props[i].prop] ? -1 : 1;
                } else {
                    return a[props[i].prop] < b[props[i].prop] ? 1 : -1;
                }
            } else {
                return 0;
            }
        }

        function compareDev(ak: IssueKey, bk: IssueKey, i: {
            [p: string]: IssueOrderInfo
        }, view: ConfigV2View): number {
            let a: IssueOrderInfo = i[ak.key];
            let b: IssueOrderInfo = i[bk.key];

            if (view.usePriority) {
                return compareIssues(a, b, [
                    {prop: "priorityOrder", asc: true},
                    {prop: "orderValue", asc: false},
                    {prop: "createdTime", asc: true},
                    {prop: "key", asc: true}
                ]);
            } else {
                return compareIssues(a, b, [
                    {prop: "orderValue", asc: false},
                    {prop: "priorityOrder", asc: true},
                    {prop: "createdTime", asc: true},
                    {prop: "key", asc: true}
                ]);
            }
        }

        function compareBus(ak: IssueKey, bk: IssueKey, i: { [p: string]: IssueInfo }, view: ConfigV2View) {
            let a: IssueInfo = i[ak.key];
            let b: IssueInfo = i[bk.key];


            if (view.usePriority) {
                if (a.priorityOrder !== b.priorityOrder) {
                    return a.priorityOrder - b.priorityOrder;
                } else if (a.statusGroup !== b.statusGroup) {
                    return b.statusGroup - a.statusGroup;
                }

                switch (a.statusGroup) {
                    case StatusGroup.Upstream:
                        switch (view.upstreamSort) {
                            case "r":
                                return compareIssues(a, b, [
                                    {prop: "orderValue", asc: false},
                                    {prop: "key", asc: true}
                                ]);
                            default:
                                return compareIssues(a, b, [
                                    {prop: "busUpstreamAge", asc: false},
                                    {prop: "key", asc: true}
                                ]);
                        }

                    case StatusGroup.Downstream:
                        switch (view.downstreamSort) {
                            case "r":
                                return compareIssues(a, b, [
                                    {prop: "orderValue", asc: false},
                                    {prop: "key", asc: true}
                                ]);
                            default:
                                return compareIssues(a, b, [
                                    {prop: "busDownstreamAge", asc: false},
                                    {prop: "key", asc: true}
                                ]);
                        }

                    default:
                        return compareIssues(a, b, [
                            {prop: "orderValue", asc: false},
                            {prop: "key", asc: true}
                        ]);

                }
            } else {
                if (a.statusGroup !== b.statusGroup) {
                    return b.statusGroup - a.statusGroup;
                }

                switch (a.statusGroup) {
                    case StatusGroup.Upstream:
                        switch (view.upstreamSort) {
                            case "r":
                                return compareIssues(a, b, [
                                    {prop: "orderValue", asc: false},
                                    {prop: "priorityOrder", asc: true},
                                    {prop: "key", asc: true}
                                ]);
                            default:
                                return compareIssues(a, b, [
                                    {prop: "busUpstreamAge", asc: false},
                                    {prop: "priorityOrder", asc: true},
                                    {prop: "key", asc: true}
                                ]);
                        }

                    case StatusGroup.Downstream:
                        switch (view.downstreamSort) {
                            case "r":
                                return compareIssues(a, b, [
                                    {prop: "orderValue", asc: false},
                                    {prop: "priorityOrder", asc: true},
                                    {prop: "key", asc: true}
                                ]);
                            default:
                                return compareIssues(a, b, [
                                    {prop: "busDownstreamAge", asc: false},
                                    {prop: "priorityOrder", asc: true},
                                    {prop: "key", asc: true}
                                ]);
                        }

                    default:
                        return compareIssues(a, b, [
                            {prop: "orderValue", asc: false},
                            {prop: "priorityOrder", asc: true},
                            {prop: "key", asc: true}
                        ]);

                }
            }


        }

        function compareEpic(ak: IssueKey, bk: IssueKey, i: {
            [p: string]: IssueOrderInfo
        }, view: ConfigV2View): number {
            let a: IssueOrderInfo = i[ak.key];
            let b: IssueOrderInfo = i[bk.key];

            if (view.usePriority) {
                return compareIssues(a, b, [
                    {prop: "priorityOrder", asc: true},
                    {prop: "orderValue", asc: false},
                    {prop: "key", asc: true}
                ]);
            } else {
                return compareIssues(a, b, [
                    {prop: "orderValue", asc: false},
                    {prop: "priorityOrder", asc: true},
                    {prop: "key", asc: true}
                ]);
            }
        }

        let view = this.config.view[this.selectedView];

        let orderInfo: OrderedInfo[] = [];

        if (view.combineEpics) {
            if (view.usePriority) {
                Object.values(this.priorities).map(v => v.order).sort().forEach(order => {
                    const e = this.epics.filter(epic => this.issues[epic.key].priorityOrder === order);
                    if (e.length > 0) {
                        orderInfo.push({issuesType: IssueType.Epic, epic: e, business: [], dev: []});
                    }

                    const b = this.business.filter(bus => this.issues[bus.key].priorityOrder === order);
                    if (b.length > 0) {
                        orderInfo.push({issuesType: IssueType.Bus, epic: [], business: b, dev: []});
                    }

                    const d = this.dev.filter(dev => this.issues[dev.key].priorityOrder === order);
                    if (d.length > 0) {
                        orderInfo.push({issuesType: IssueType.Dev, epic: [], business: [], dev: d});
                    }
                });
            } else {
                if (this.epics.length > 0) {
                    orderInfo.push({issuesType: IssueType.Epic, epic: [...this.epics], business: [], dev: []});
                }
                if (this.business.length > 0) {
                    orderInfo.push({issuesType: IssueType.Bus, epic: [], business: [...this.business], dev: []});
                }
                if (this.dev.length > 0) {
                    orderInfo.push({issuesType: IssueType.Dev, epic: [], business: [], dev: [...this.dev]});
                }
            }
        } else {
            let allBus = [...this.business];
            this.epics.forEach(epic => allBus.push(...epic.business));

            if (view.usePriority) {
                Object.values(this.priorities).map(v => v.order).sort().forEach(order => {
                    const b = allBus.filter(bus => this.issues[bus.key].priorityOrder === order);
                    if (b.length > 0) {
                        orderInfo.push({issuesType: IssueType.Bus, epic: [], business: b, dev: []});
                    }

                    const d = this.dev.filter(dev => this.issues[dev.key].priorityOrder === order);
                    if (d.length > 0) {
                        orderInfo.push({issuesType: IssueType.Dev, epic: [], business: [], dev: d});
                    }
                });
            } else {
                if (this.business.length > 0) {
                    orderInfo.push({issuesType: IssueType.Bus, epic: [], business: [...this.business], dev: []});
                }
                if (this.dev.length > 0) {
                    orderInfo.push({issuesType: IssueType.Dev, epic: [], business: [], dev: [...this.dev]});
                }
            }
        }

        orderInfo.forEach(o => {
            switch (o.issuesType) {
                case IssueType.Epic:
                    o.epic.forEach(epic => epic.business.forEach(bus => bus.devIssues.sort((a, b) => compareDev(a, b, this.issues, view))));
                    o.epic.forEach(epic => epic.business.sort((a, b) => compareBus(a, b, this.issues, view)));

                    this.epics.forEach(epic =>
                        this.issues[epic.key].orderValue = epic.business.map(bus => this.issues[bus.key].orderValue)
                            .reduce((acc, val) => Math.min(acc, val)), Number.MAX_VALUE);

                    o.epic.sort((a, b) => compareEpic(a, b, this.issues, view));
                    break;
                case IssueType.Bus:
                    o.business.forEach(bus => bus.devIssues.sort((a, b) => compareDev(a, b, this.issues, view)));
                    o.business.sort((a, b) => compareBus(a, b, this.issues, view));
                    break;
                case IssueType.Dev:
                    o.dev.sort((a, b) => compareDev(a, b, this.issues, view));
                    break;
            }
        });


        for (let i = orderInfo.length - 1; i > 0; i--) {
            if (orderInfo[i - 1].issuesType === orderInfo[i].issuesType) {
                orderInfo[i - 1].epic.push(...orderInfo[i].epic);
                orderInfo[i - 1].business.push(...orderInfo[i].business);
                orderInfo[i - 1].dev.push(...orderInfo[i].dev);
                delete orderInfo[i];
            }
        }

        this.orderInfo = orderInfo;

    }
}