import React, {useState} from 'react';
import './App.css';
import queryString from 'query-string';
import {ConfigEditor} from "./ConfigEditor";
import {SimpleCallback} from "./CommonTypes";
import {MainTable} from "./MainTable";

function App() {

    const [showConfig, setShowConfig] = useState(false);
    const hideEditorCallback: SimpleCallback = () => setShowConfig(false);
    const showEditorCallback: SimpleCallback = () => setShowConfig(true);


    const parsed = queryString.parse(window.location.hash.substring(1));

    const baseUrlParam = parsed["base"];
    const businessBoardIdParam = parsed["board"]!;

    const baseUrl: string = baseUrlParam ? baseUrlParam.toString() : "";
    const businessBoardId: string = businessBoardIdParam ? businessBoardIdParam.toString() : "";

    return (
        <div className="App">

            {showConfig &&
                <ConfigEditor baseUrl={baseUrl} businessBoardId={businessBoardId} hideConfig={hideEditorCallback}
                              key="ConfigEditor"/>
            }
            {!showConfig &&
                <MainTable baseUrl={baseUrl} businessBoardId={businessBoardId} showConfigCallback={showEditorCallback}
                           key="MainTable"/>}
        </div>
    );
}

export default App;
